@font-face {
  font-family: "ShinGoBold";
  src: local("ShinGoBold"), url("./fonts/ShinGo/ShinGoBold.otf") format("opentype");
  font-weight: bold;
}
@font-face {
  font-family: "RodinBokutohBold";
  src: local("RodinBokutohBold"), url("./fonts/RodinBokutoh/RodinBokutohB.otf") format("opentype");
  font-weight: bold;
}

html {
  min-height: 100%; /* Look, it's not fixed anymore! */

  display: flex;
  flex-direction: column;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 
    'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: repeating-linear-gradient(
    #f5f5f5, #f5f5f5 3px, #e4e4e4 3px, #e4e4e4 9px
  );
  cursor: url(./wii_pointer.cur) 11 3, auto;
  flex-grow: 1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::selection {
  background: #34BEED; /* WebKit/Blink Browsers */
  color: black;
}

::-moz-selection {
  background: #34BEED; /* Gecko Browsers */
  color: black;
}